.container{
    max-width:1200px;
}

.home-page-container {
    border: 1px outset black;
    text-align: center;
    height: 300px;
}


.home-page-heading {
    color: rgb(196, 36, 36);
    justify-self: start;
    margin-left: 20px;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
}


.featureBlog { 
    display:flex; flex-direction:row;

}

.leftHome img{
    max-width: 90%;  
height: auto;  
border-radius: 10px;
}

.leftHome {
    float: left; 
    width: 40%; 
}

.rightHome {
    float: right; 
  width: 60%; 
}

.rightHome h1{
    font-size: 2rem !important;
    text-decoration: none;
}

.rightHome a{
    color: black;
    text-decoration: none;
}