.blog-detail{
    min-height: 100vh;
}

.blogDetailPage {
    margin: auto;
    width: 80%;
    padding: 10px;
}


.blog-title{
    color: black;
    font-size: 40px;
    /* font-weight: bold; */

}

.title-banner{
    border-radius: 10px;
    margin-bottom: 10px;
    margin-top: 10px;
}

.blogContent p{
    font-size: 18px;
}

.blogContent h3{
    font-size: 22px;
}

.blogContent h2{
    font-size: 24px;
}

.published-on span{
    color: grey;
    font-size: 16px;
}

.comment-section p{
    font-size: 22px;
}

.advertisement{
    border-radius: 10px;
    background: #fff;
    box-shadow: 0 0 6px 0 rgb(0 0 0 / 15%);
    position: sticky;
}

.advertisement p{
    text-align: center;
}

.blog-category-name{
    color: #5d5dc8;
    font-size: 16px;
    text-transform:lowercase;
    cursor: pointer;
    margin-bottom: 15px;
}

.blog-category-name::first-letter{
    text-transform: uppercase;
}