.profile{
  max-width: 300px;
  margin: auto;
  text-align: center;
  margin-top: 10%;
  height: 100vh;

}

.profile-box {
  background-color: #fff;
  padding: 20px;
  text-align: center;
  width: 300px;
}


.profile-box img {
  border-radius: 50%;
  height: 120px;
  margin: 0 auto 20px;
  width: 120px;
}

.profile-box h2 {
  font-size: 24px;
  margin: 0 0 10px;
  text-transform: capitalize;
}

.profile-box p {
  font-size: 14px;
  margin: 0;
}

.social-links {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 20px;

}

.social-links li {
  display: inline-block;
  margin-right: 10px;
}

.social-links li a {
  color: #333;
  font-size: 18px;
}


